        * {
            margin: 0;
            padding: 0;
            resize: none;
            block-size: auto;
        }
        
        body {
            background-image: url('src/gifbg3.gif');
            /* background-repeat: no-repeat;
            background-attachment: fixed; */
            background-attachment: fixed;
            /* overflow: hidden; */
            width: auto;
            height: auto;
            background-repeat: no-repeat;
            background-size: 100%;
        }
        
        html {
            scroll-behavior: smooth;
        }
        
        main.mySlides {
            display: none;
        }
        
        img {
            vertical-align: middle;
        }
        /* Slideshow container */
        
        .slideshow-container {
            max-width: 1000px;
            position: relative;
            margin: auto;
        }
        /* The dots/bullets/indicators */
        
        .dot {
            height: 15px;
            width: 15px;
            margin: 0 2px;
            background-color: rgb(158, 155, 155);
            border-radius: 50%;
            display: inline-block;
            transition: background-color 0.6s ease;
        }
        
        .active {
            background-color: #717171;
        }
        /* Fading animation */
        
        .fade {
            -webkit-animation-name: fade;
            -webkit-animation-duration: 1.5s;
            animation-name: fade;
            animation-duration: 1.5s;
        }
        
        @-webkit-keyframes fade {
            from {
                opacity: .4
            }
            to {
                opacity: 1
            }
        }
        
        @keyframes fade {
            from {
                opacity: .4
            }
            to {
                opacity: 1
            }
        }
        
