        /* hide menu */
        
        #navbar {
            background-color: rgb(1, 7, 8);
            position: sticky;
            top: 0;
            width: 100%;
            transition: top 0.3s;
            z-index: 1;
        }
        /* Style the navbar links */
        
        #navbar a {
            background-color: rgb(1, 7, 8);
            /* float: left; */
            display: inline-block;
            color: white;
            text-align: center;
            padding: 13px;
            text-decoration: none;
            font-size: 1rem;
            border: 1px unset rgb(29, 28, 28);
            z-index: 1;
        }
        #txt {
